<!--                  resize="none">-->
<!--        </el-input>-->
        <v-app class="choice" v-if="item.type==='completion'">
          <v-text-field
              v-model="item.answer"
              label="请在此输入答案~"
              single-line
          ></v-text-field>
        </v-app>

        <v-app class="choice" style="margin-top: 20px" v-if="item.type==='scoring'">
          <v-card-text>
            <v-slider
                v-model="item.answer"
                step="1"
                thumb-label
                ticks="always"
                :max="item.option_list.length - 1"
            ></v-slider>
          </v-card-text>
        </v-app>
      </div>
      <!--内容结束-->

      <el-button type="primary" @click="click">提交</el-button>

<!--      <div class="line"></div>-->
