<script>
import authorization from "../utils/authorization";
import axios from "axios";

export default {
  name: "FillQuestionnaire",
  components: {},
  data(){
    return {
      single: 0,
      multiple: [],
      questionnaire_id: 1,
      content: '',
      info: '',
      answer_list: [],
      flag: true,
    }
  },
  mounted() {
    const that = this;
    console.log(this.$route.params.id);
    axios
        .get('/api/questionnaire/' + that.$route.params.id + '/')
        .then(response => (this.info = response.data))
        .catch(function (error){
          that.$notify.error({
            title: '好像发生了什么错误',
            message: error.message
          })
        })
  },
  methods: {
    // init() {
    //   console.log("zhixing");
    //   let i = 0;
    //   for( let a in this.info.question_list) {
    //     if(a.type === 'single-choice') {
    //       let t = 'aaa';
    //       this.answer_list[i++] = t;
    //     }
    //     else if(a.type === 'multiple-choice') {
    //       let t = [];
    //       this.answer_list[i++] = t;
    //     }
    //     else {
    //       let t = 'bbb';
    //       this.answer_list[i++] = t;
    //     }
    //   }
    // },
    click(){
      let tmp;
      const that = this;
      if(this.info.status==='closed'){
        this.$notify.error({
          title: '问卷已关闭',
          message: '无法提交'
        })
      }
      else if(this.info.status==='deleted'){
        this.$notify.error({
          title: '问卷已删除',
          message: '无法提交'
        })
      }
      else{
        this.answer_list.splice(0, this.answer_list.length);
        this.flag = true;
        for(let item of this.info.question_list){
          if(item.type === 'multiple-choice'){
            tmp = false;
            for(let i of item.option_list){
              if(i.is_answer_choice === true){
                let data = {
                  questionnaire: that.info.id,
                  question: item.id,
                  option: i.id,
                };
                that.answer_list.push(data);
                tmp = true;
                // console.log(i.title);
              }
            }
            if(tmp === false && item.is_must_answer) {
              that.flag = false;
              console.log('多选')
            }
          }
          else if(item.type === 'single-choice' || item.type === 'scoring'){
            tmp = false;
            for(let i of item.option_list){
              if(item.option_list.indexOf(i) === item.answer){
                let data = {
                  questionnaire: that.info.id,
                  question: item.id,
                  option: i.id,
                };
                tmp = true;
                that.answer_list.push(data);
              }
            }
            if(tmp === false && item.is_must_answer) {
              that.flag = false;
              console.log('单选 | 评分')
            }
            // console.log(item.answer);
          }
          else if(item.type === 'completion'){
            if(item.answer === '' && item.is_must_answer) {
              that.flag = false;
              console.log('填空')
            }
            else {
              let data = {
                questionnaire: that.info.id,
                question: item.id,
                option: item.option_list[0].id,
                content: item.answer,
              };
              that.answer_list.push(data);
            }
          }
        }
        console.log(that.answer_list);
        if(that.flag === true) {
          // 提交问卷
          axios
              .post('/api/answer/', that.answer_list)
              .then(function (response){
                // that.$notify.success({
                //   title: '问卷提交成功',
                //   message: '芜湖'
                // })
                that.$router.push({path: '/thank/'});
              })
              .catch(function (error){
                that.$notify.error({
                  title: '好像发生了什么错误',
                  message: error.message
                })
              })
        }
        else {
          that.$notify.warning({
            title: '请完成所有的必选项题目!',
          })
        }

      }
    },
  }
}
</script>



